.project {
    margin: 123px 0 0 0;

    button {
        padding: 15px 30px;
        background-color: black;
        border: none;
        color: white;
        font-weight: 600;
        border-radius: 5px;
        cursor: pointer;
        transition: box-shadow 0.3s ease;
        font-family: 'Figtree', sans-serif;
        font-size: 16px;
      
        &:hover {
            box-shadow: 8px 8px 0 1px #ddd;
        }
      }

    .info {
        flex: 1;

        h2 {
            font-weight: 500;
            font-size: 38px;
            bottom: 0 0 44px 0;
        }

        .pill-container {
            width: 100%;
            display: block;
            margin-bottom: 28px;
            max-width: 300px;

            &::after {
                content: "";
                display: table;
                clear: both;
            }

            .pill {
                margin: 8px 8px 0 0;
                float: left;
                font-size: 14px;
                padding: 3px 5px;
                border-radius: 5px;
                background-color: #f2f6f7;
                color: #7A8B97;
            }
        }

        p {
            margin: 0 0 38px 0;
            font-weight: 500;
            font-size: 18px;
        }
    }

    .display {
        flex: 1;
        transition: all 0.3s ease-in;

        img {
            width: 100%;
            border-radius: 10px;
            box-shadow: 0 4px 30px 5px rgba(0,0,0, 0.10);
            transition: all 0.3s ease-in;
        }

        &:hover {
            transform: rotate(3deg);

            img {
                box-shadow: 0 4px 20px 5px rgba(0,0,0, 0.25);
            }
        }
    }
}